import React from 'react';
import clsx from 'clsx';
import ButtonGroup, { ButtonGroupProps } from '@elementor/ui/ButtonGroup';
import Button, { ButtonProps } from '@elementor/ui/Button';
import SvgIcon from '@elementor/ui/SvgIcon';
import { styled, useThemeProps, createSlots } from '@elementor/ui/styles';
import './SplitButton.types';

export interface SplitButtonProps extends Omit<ButtonGroupProps, 'variant'> {
	CaretButtonProps?: ButtonProps;
	MainButtonProps?: ButtonProps;
	onClick?: ( event: React.MouseEvent<HTMLElement> ) => void;
	variant?: Exclude<ButtonProps['variant'], 'text'>;
	slotProps?: {
		mainButton?: ButtonProps;
		caretButton?: ButtonProps;
	},
}

// TODO: replace to icon from a dedicated package once available.
const ArrowDropDownIcon = () => {
	return (
		<SvgIcon viewBox="0 0 24 24" sx={ { fill: '#fff' } }>
			<path fillRule="evenodd" clipRule="evenodd" d="M17.2929 9.29289C17.6834 8.90237 18.3166 8.90237 18.7071 9.29289C19.0976 9.68342 19.0976 10.3166 18.7071 10.7071L12.7071 16.7071C12.3166 17.0976 11.6834 17.0976 11.2929 16.7071L5.29289 10.7071C4.90237 10.3166 4.90237 9.68342 5.29289 9.29289C5.68342 8.90237 6.31658 8.90237 6.70711 9.29289L12 14.5858L17.2929 9.29289Z" />
		</SvgIcon>
	);
};

const { slots, classNames } = createSlots( 'SplitButton', [ 'root', 'mainButton', 'caretButton' ] );

export type SplitButtonSlots = keyof typeof slots;

const Root = styled( ButtonGroup, slots.root )<{ ownerState: SplitButtonProps }>( {} );

const MainButton = styled( Button, slots.mainButton )<{ ownerState: SplitButtonProps }>( {} );

const CaretButton = styled( Button, slots.caretButton )<{ ownerState: SplitButtonProps }>( () => ( { paddingInline: 0 } ) );

const SplitButton = React.forwardRef<HTMLDivElement, SplitButtonProps>( ( inProps, ref ) => {
	const props = useThemeProps( { props: inProps, name: slots.root.name } );

	const { onClick, children, MainButtonProps = {}, CaretButtonProps = {}, slotProps = {}, ...rootProps } = props;

	return (
		<Root
			{ ...rootProps }
			ref={ ref }
			className={ clsx( [ [ classNames.root, rootProps.className ] ] ) }
			ownerState={ props }
		>
			<MainButton
				onClick={ onClick }
				{ ...MainButtonProps }
				{ ...slotProps.mainButton }
				className={ clsx( [ classNames.mainButton, MainButtonProps.className, slotProps.mainButton?.className ] ) }
				ownerState={ props }
			>
				{ children }
			</MainButton>

			<CaretButton
				{ ...CaretButtonProps }
				{ ...slotProps.caretButton }
				className={ clsx( [ classNames.caretButton, CaretButtonProps.className, slotProps.caretButton?.className ] ) }
				ownerState={ props }
			/>
		</Root>
	);
} ) as React.FC<SplitButtonProps>;

SplitButton.defaultProps = {
	variant: 'contained',
};

CaretButton.defaultProps = {
	children: <ArrowDropDownIcon />,
};

export default SplitButton;
