// Adding Mui prefix: ComponentName -> MuiComponentName.
type ThemedComponentName<T extends string> = `Mui${ T }`;

// Adding Mui prefix and a slot-name suffix: ComponentName -> MuiComponentName-slotName.
type ThemedClassName<TName extends string, TSlot extends string> = `${ ThemedComponentName<TName> }-${ TSlot }`;

export const createSlots = <TName extends string, TSlot extends string>(
	componentName: TName,
	componentSlots: TSlot[],
) => {
	const slots = {} as {
		[Slot in TSlot]: { slot: Slot, name: ThemedComponentName<TName> };
	};

	const classNames = {} as {
		[Slot in TSlot]: ThemedClassName<TName, Slot>;
	};

	componentSlots.forEach( ( slot ) => {
		classNames[ slot ] = `Mui${ componentName }-${ slot }`;

		slots[ slot ] = {
			slot,
			name: `Mui${ componentName }`,
		};
	} );

	return {
		slots,
		classNames,
	};
};
