import React from 'react';
import { default as MuiButtonGroup, ButtonGroupProps } from '@mui/material/ButtonGroup';

const ButtonGroup = React.forwardRef<HTMLElement, ButtonGroupProps>( ( props, ref ) => {
	return (
		<MuiButtonGroup
			{ ...props }
			ref={ ref as React.MutableRefObject<null> }
		/>
	);
} ) as React.FC<ButtonGroupProps>;

export default ButtonGroup;
